body {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	margin: 0 0 0 0;
	padding: 0 0 0 0;
}

*,
*:before,
*:after {
	-webkit-box-sizing: inherit;
	-moz-box-sizing: inherit;
	box-sizing: inherit;
	-webkit-font-smoothing: antialiased;
}

.main {
	background: hsl(0, 0%, 100%, 1);
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    height: 100vh;
	margin: 0 0 0 0;
	overflow: hidden;
    padding: 0 0 0 0;
    position: relative;
    width: 100vw;
}